.layout{
    box-sizing: border-box;
}
.page-header{
    padding: 16px 32px 0;
    background: #fff;
    border-bottom: 1px solid #e8e8e8;
}
.page-header-title-view{
    display: inline-block;
}
.page-header-content-view{
    padding-top: 12px;
}
.page-content-loading{
    margin: 24px 24px 0;
    background:#fff;
}
@media (max-width: 480px){
.page-content {
    width: 100%;
    overflow-x: auto;
}}