.header {
    background-color: #fff;
    height: 64px;
    line-height: 64px;
    padding: 0;
    box-shadow: 0 1px 4px rgba(0,21,41,.08);
    position: relative;
    z-index: 2;
}

.ant-drawer-body {
    padding: 0;
}

.header-drawer {
    padding: 0;
    height: '100vh';
}

.ant-drawer-wrapper-body {
    background: #001529;
    overflow-x: hidden !important;
    overflow-y: auto;
}

.header-menu {
    width: 256px;
}

.header-menu-logo {
    height: 64px;
    padding-left: 24px;
    line-height: 64px;
    background: #002140;
}

.header-menu-logo img {
    display: inline-block;
    height: 32px;
    vertical-align: middle;
}

.header-menu-logo h1 {
    display: inline-block;
    margin: 0 0 0 12px;
    color: white;
    font-weight: 600;
    font-size: 20px;
    font-family: Avenir, 'Helvetica Neue', Arial, Helvetica, sans-serif;
    vertical-align: middle;
}

.ant-menu-item a {
    color: rgba(255, 255, 255, 0.65);
}

.ant-menu-item a:before {
    position: absolute;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    background-color: transparent;
    content: "";
}

.ant-menu-item a:hover, .ant-menu-item-selected a {
    color: #fff
}

.icon-collapsed {
    line-height: 64px;
    padding: 16px 24px;
    font-size: 24px;
    cursor: pointer;
}

.icon-collapsed:hover {
    background: rgba(0,0,0,0.025);
}

.header-right {
    float: right;
    height: 100%;
    padding-right: 24px;
}

.user-info {
    display: inline-block;
    height: 100%;
    padding: 0 12px;
    cursor: pointer;
}

.user-info:hover {
    background: rgba(0,0,0,0.025);
}

.avatar {
    margin: 20px 0;
    margin-right: 8px;
    color: #1890FF;
    vertical-align: top;
    background: #87d068;
}