}
.ant-page-header {
    padding: 16px 32px 0;
    border-bottom: 1px solid #e8e8e8;
}

.ant-page-header-title-view-title {
    font-weight: 600;
    font-size: 20px;
}
.nv-page-content-loading{
    margin: 24px 24px 0;
    background:#fff;
}
