#uploadimg .ant-page-header {
    padding: 16px 32px 0;
    border-bottom: 1px solid #e8e8e8;
}

#uploadimg .ant-page-header-title-view-title {
    font-weight: 600;
    font-size: 20px;
}

.uploadimg-content {
    margin: 24px 24px 0;
}

.uploadimg-content .ant-form-item {
    display: flex;
}

.uploadimg-content .ant-form-item-label {
    width: auto;
    padding-right: 8px;
    line-height: 32px;
    display: inline-block;
    vertical-align: top;
}

.uploadimg-content .ant-form-item-control-wrapper {
    flex: 1 1;
    display: inline-block;
    vertical-align: top;
}